import classNames from 'classnames';
import { Link } from 'react-router-dom';
import { PropsTab } from './PropsTab';

export const Tab: React.FC<PropsTab> = ({ tab, selectedTabId }) => {
  const isActive = `Tab-${tab.id}` === `Tab-${selectedTabId}`;

  return (
    <li
      data-cy="Tab"
      key={tab.id}
      className={classNames(
        { 'is-active': isActive },
      )}
    >
      <Link
        to={`/tabs/${tab.id}`}
        data-cy="TabLink"
      >
        {tab.title}
      </Link>
    </li>
  );
};
